.App {
  text-align: center;
  font-family: 'BioRhyme', serif;
}
.Header {
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    /* color: #25D366; */
    font-family: 'BioRhyme', serif;
    margin:2%;
    color: #2C3A47;
}
.Header h1 {
    font-size: 400%;
}
.freeCodeCampIcon {
    height: 80px;
    margin: 20px;
    border-radius: 50%;
}
.Navbar {
    display:flex;
    justify-content: space-around;
    align-items: center;
    background-color:#2C3A47;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1;
}
.navIcon {
    height: 60px;
    background-color: white;
    border-radius: 50%;
    margin: 20%;
}
.searchContainer {
    position: relative;
}
input[type="search"] {
    
	display: block; 
    width: 100%; 
    font-family: 'Permanent Marker', cursive;
	letter-spacing: 1px;        
	text-shadow: 0 0 2px #2C3A47;       
    font-size: large;
    border: 1px solid #2C3A47;
    border-radius: 100px;
    padding: 2px 23px 2px 30px;
    outline: 0;
    height: 25px;
}
input[type="submit"] {  
	display: block; 
    width: 50%; 
    font-family: 'Architects Daughter', cursive;
    letter-spacing: 1px;     
	text-shadow: 0 0 2px black;       
    border: 1px solid aliceblue;
    border-radius: 100px;
    height: 25px;
    cursor: pointer;
}
input[type="submit"]:hover {  
    color: black;   
    border: 1px solid black;
    font-weight: 900;
	text-shadow: 0 0 2px white;       
}
.search-icon {
    position: absolute;
    top: 6px;
    left: 8px;
    width: 14px;
}
.optionsSearch {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    color: white;
    text-shadow: 0 0 0.5px black;
    width: 100%;
}
.optionLink {
    background-color: #2C3A47;
    color: white;
    margin: 5px auto;
    cursor: pointer;
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 100px;
    font-family: 'Permanent Marker', cursive;
}
.optionName {
    width: 90%;
}
.optionIconDiv {
    width: 10%;
    margin: 5px;
}
.optionIcon {
    width: 100%;
    border-radius:50%;
    background-color: white;
}
.error {
    color: red;
}

#userAddForm {
    text-align: center;
}

#userUpload {
    text-align: center;
}

#userLabel {
    font-weight: bold;
    font-size: 130%;
}


.miniLoader {
    border: 8px solid #f3f3f3;
    border-radius: 50%;
    border-top: 8px solid #2C3A47;
    width: 60px;
    height: 60px;
    -webkit-animation: spin 2s linear infinite;
    /* Safari */
    animation: spin 2s linear infinite;
    margin: 10% auto;
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
footer {
    background: #2C3A47;
    bottom: 0;
    width: 100%;
    margin: 50px 0;
    padding: 10px;
    color: white;
}
.ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    top: 0;
    z-index: 1;
    list-style-type: none;
}
.Icon {
    height: 50px;
    width: 50px;
    transition: 1500ms;
}
.Icon:hover {
    border-radius: 50%;
    border: 1px solid aliceblue;
    background-color: aliceblue;
    transition: 1500ms;
    height: 150%;
}
.circle {
    border-radius: 50%;
} 
.name {
    color: white;
    -webkit-text-stroke: 0.2px black;
    transition: 1500ms;
}
.name:hover {
    color: rgb(243, 204, 145);
    transition: 1500ms;
}
.about {
    font-family: 'Permanent Marker', cursive;
    font-size: 120%;
}
.freeCodeCampImage {
    background-color: #2C3A47;
    /* border-radius: 10%; */
    max-width: 40%;
    margin: 3%;
    transition: 2s;
    cursor: pointer;
}
.freeCodeCampImage:hover {
    border-radius: 40%;
    transition: 2s;
}

